			},
			// 读取excel文件的方法
			importfxx(obj) {
				let _this = this;
				let inputDOM = this.$refs.inputer; // 通过DOM取文件数据
				this.file = event.currentTarget.files[0];
				var rABS = false; //是否将文件读取为二进制字符串
				var f = this.file;
				var reader = new FileReader();
				FileReader.prototype.readAsBinaryString = function(f) {
					var binary = "";
					var rABS = false; //是否将文件读取为二进制字符串
					var pt = this;
					var wb; //读取完成的数据
					var outdata;
					var reader = new FileReader();
					reader.onload = function(e) {
						var bytes = new Uint8Array(reader.result);
						var length = bytes.byteLength;
						for (var i = 0; i < length; i++) {
							binary += String.fromCharCode(bytes[i]);
						}
						//如果没有在main.js中引入，则此处需要引入，用于解析excel
						var XLSX = require("xlsx");
						if (rABS) {
							wb = XLSX.read(btoa(fixdata(binary)), { //手动转化
								type: "base64"
							});
							console.log(wb)
						} else {
							wb = XLSX.read(binary, {
								type: "binary"
							});

						}
						outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata就是读取的数据（不包含标题行即表头，表头会作为对象的下标）
						//此处可对数据进行处理
						let arr = [];
						outdata.map(v => {
							let obj = {}
							obj.DateID = v['DateID']
							obj.PictureID = v['PictureID']
							obj.Date = v['Date']
							obj.Name = v['Name']
							obj.IDCardNo = v['IDCardNo']
							obj.Sex = v['Sex']
							obj.Age = v['Age']
							obj.Nation = v['Nation']
							obj.Schooling = v['Schooling']
							obj.UnitProperty = v['UnitProperty']
							obj.UnitName = v['UnitName']
							obj.WorkType = v['WorkType']
							obj.WorkTime = v['WorkTime']
							obj.PhoneNo = v['PhoneNo']
							arr.push(obj)
						});
						_this.tableDatas = arr;
						return arr;
					};
					reader.readAsArrayBuffer(f);
				}
				if (rABS) {
					reader.readAsArrayBuffer(f);
				} else {
					reader.readAsBinaryString(f);
				}
				this.tableDatas = _this.tableDatas;
				setTimeout(()=>{
					this.ReserveBatch()					
				},500)
			}
		}
